<template>
  <section class="payplugUiBlock descriptionBlock" data-e2e-name="blockDescription">
    <div class="_content">
      <div class="_header">
        <PayplugImage :imageSrc="imageSrcLogo" imgParentClass="_logo"/>
        <div class="_version">
          <Paragraph class="_descriptionVersion" :paragraphText="plugin_version" />
          <SelectBox :selectName="select_name" :selectOptions="options" :class="'_show'"/>
        </div>
      </div>
      <Paragraph class="_descriptionTitle" :paragraphText="plugin_title" />
      <Paragraph class="_descriptionText" :paragraphText="plugin_description" />
    </div>
  </section>

</template>

<script>

import PayplugImage from "../atoms/AtomImage";
import SelectBox from "../atoms/AtomSelect";
import Paragraph from "../atoms/AtomParagraph";
import {store} from "@/store/store";

export default {
  name: "OrganismSectionHeader",
  components:{
    SelectBox, Paragraph, PayplugImage
  },
  data(){
    return {
      store: store,
      "plugin_title":this.jsonData.header.title,
      "plugin_version": '',
      "plugin_description":'',
      "options": [],
      "imageSrcLogo": "svg/logo-payplug.svg",
      "select_name": "",
    }
  },
  created() {
    this.init()
  },
  updated() {
    this.init()
  },
  methods: {
    init(){
      let payplug_enable = null
      this.plugin_description = this.jsonData.header.descriptions[this.store.data.environment].description;
      this.plugin_version = this.jsonData.header.descriptions[this.store.data.environment].plugin_version;
      this.jsonData.header.options.map(function(v){
        if(v.name === "payplug_enable"){
          payplug_enable = v;
        }
      });
      this.options = payplug_enable.options;
      this.select_name = this.jsonData.header.options[0].name;
    }
  }
}
</script>

