import { reactive } from 'vue'

export const store = reactive({
    // all data that will be used for the different requests
    settings: {
        "ajax_url": "",
        "nonce": ""
    },
    // all data that will be sent in the save request
    data: {
        "environment": "sandbox"
    },
    // all data that will be sent in the login request
    login: {
        "payplug_email": "",
        "payplug_password": ""
    },
    // all data that will be sent in the check permission request
    permission: {
        "payment_method": ""
    },
    storeValue(name, value, action = "data") {
        this[action][name] = value
    }
})