var qs = require('qs');
import axios from "axios";
import {store} from "@/store/store";

export async function login() {
    var data = store.login;
    data["action"] = store.settings.login_action;
    data["nonce"] = store.settings.nonce;
    const response = await axios.post(store.settings.ajax_url, qs.stringify(data));
    return response;
}

export async function save() {
    var data = store.data;
    data["action"] = store.settings.save_action;
    data["nonce"] = store.settings.nonce;
    const response = await axios.post(store.settings.ajax_url, qs.stringify(data));
    return response;
}

export async function check_permission() {
    var data = store.permission;
    data["action"] = store.settings.check_permission;
    data["nonce"] = store.settings.nonce;
    const response = await axios.post(store.settings.ajax_url, qs.stringify(data));
    return response;
}
