<template>
  <Switch :switchName="paymentMethod.name" :switchChecked="paymentMethod.checked ? true : null"
          :switchData="'paymentMethod_' + paymentMethod.name"/>
  <Image :imageSrc="paymentMethod.image" imgParentClass="_logo"/>
  <PMethodHeader :description="description" :title="paymentMethod.title" :links="links" />
  <PMethodInformations :environment="environment" :paymentMethod="paymentMethod" />
</template>

<script>
import Switch from "@/components/atoms/AtomSwitch";
import Image from "@/components/atoms/AtomImage";
import PMethodHeader from "@/components/organisms/PMethod/OrganismPMethodHeader";
import PMethodInformations from "@/components/organisms/PMethod/OrganismPMethodInformations";

export default  {
  name: "OrganismPMethodFull",
  components:{ Switch, Image, PMethodHeader, PMethodInformations },
  props: ["environment", "paymentMethod"],
  created() {
    this.init()
  },
  updated() {
    this.init()
  },
  methods: {
    init(){
      this.description = this.paymentMethod.descriptions[this.environment].description;
      this.links = this.paymentMethod.descriptions[this.environment].link_know_more ?? null;
      this.payment_methods = this.paymentMethod.descriptions.options;
    },
    showRegistration(){
      this.$emit('notRegisted', true)
    },
    showLoggedIn(){
      this.$emit('showLoggedIn', true)
    }
  }
}
</script>

