<template>
  <section class="payplugUiBlock generalBlock -logged" data-e2e-name="generalLogged">
    <BlockHeader :blockHeaderTitle="title" :blockHeaderDescription="description" />
    <div class="_content">
      <div class="_user">
        <div class="_userInformations">
          {{ email }}
          <Tooltip data-e2e-name="payplugTooltipLogout">
            <payplugUIButton class="payplugUIAction _userLogout"
                             buttonName="logout" :buttonText="lougout"
                             @click.prevent="showLogin"
                             data-e2e-name="payplugLogout"/>
          </Tooltip>
        </div>
        <ButtonLink class="_userPortal -icon" :buttonLinkText="link_access_portal_text" :buttonLinkHref="link_access_portal_href" />
      </div>
      <div class="_sandbox">
        <Title class="_sandboxTitle" :titleText="mode"/>
        <ParagraphAnchors class="_sandboxDescription -test" :paragraphAnchorsText="mode_description" :paragraphAnchorsLinks="[link_learn_more]" paragraphAnchorsData="faq" />
        <payplugUIIOptions class="_sandboxRadioButton" :optionsItems="options" :optionsName="options[0].name" optionsData="sandbox" />
      </div>
    </div>
  </section>
</template>

<script>
import BlockHeader from "../molecules/MoleculeBlockHeader";
import Tooltip from "../molecules/MoleculeTooltip";
import payplugUIButton from "../atoms/AtomButton";
import ButtonLink from "../atoms/AtomButtonLink";
import Title from "../atoms/AtomTitle";
import Anchor from "../atoms/AtomAnchor";
import payplugUIIOptions from "../atoms/AtomOptions";
import ParagraphAnchors from "@/components/molecules/MoleculeParagraphAnchors";
import {store} from "@/store/store";

export default {
  name: "OrganismSectionLoggedIn",
  components: { payplugUIButton, BlockHeader, Tooltip, ButtonLink, Anchor, payplugUIIOptions, Title, ParagraphAnchors },
  props: ["environment"],
  data(){
    return {
      store: store,
      title: this.jsonData.logged.title,
      description: "",
      email: this.jsonData.settings.email,
      lougout:"",
      link_access_portal_text: "",
      link_access_portal_href: "",
      mode: "",
      mode_description: "",
      link_learn_more: {},
      options: null
    }
  },
  created() {
    this.init()
    let that = this;
    this.jsonData.logged.options.map(function(value){
      if(that.jsonData.settings.mode === value.value) {
        that.store.storeValue("environment", (value.value === 0) ? "live" : "sandbox");
      }
    });
  },
  updated() {
    this.init()
  },
  methods: {
    init(){
      let that = this.jsonData;
      this.description = this.jsonData.logged.descriptions[this.environment].description;
      this.lougout = this.jsonData.logged.descriptions[this.environment].lougout;
      this.link_access_portal_text = this.jsonData.logged.descriptions[this.environment].link_access_portal.text;
      this.link_access_portal_href = this.jsonData.logged.descriptions[this.environment].link_access_portal.url;
      this.mode = this.jsonData.logged.descriptions[this.environment].mode;
      this.mode_description = this.jsonData.logged.descriptions[this.environment].mode_description;
      this.link_learn_more = this.jsonData.logged.descriptions[this.environment].link_learn_more;
      this.jsonData.logged.options.map(function(value){
        if(that.settings.mode === value.value) {
          value.checked = true;
        }
      });

      this.options = this.jsonData.logged.options;
    },
    showLogin() {
      this.$emit('showLogin', true)
    },
  }
}
</script>

