<template>
  <section class="payplugUiBlock paylaterBlock" data-e2e-name="blockPaylater">
    <BlockHeader :blockHeaderTitle="title" :blockHeaderDescription="description" />
    <div class="_content">
      <div class="paymentOption" :class="'-' + paylater.name" :key="paylater">
        <PaylaterFull :environment="environment" :paylater="paylater" />
      </div>
    </div>
  </section>
</template>

<script>
import BlockHeader from "@/components/molecules/MoleculeBlockHeader";
import PaylaterFull from "@/components/organisms/Paylater/OrganismPaylaterFull";

export default {
  name: "OrganismSectionPaylater",
  components:{ BlockHeader, PaylaterFull },
  props: ["environment"],
  data(){
    return {
      "title": this.jsonData.paylater.title,
      "description": null
    }
  },
  created() {
    this.init()
  },
  updated() {
    this.init()
  },
  methods: {
    init(){
      this.description = this.jsonData.paylater.descriptions[this.environment].description;
      this.paylater = this.jsonData.paylater.options[0];
    },
    showRegistration(){
      this.$emit('notRegisted', true)
    },
    showLoggedIn(){
      this.$emit('showLoggedIn', true)
    }
  }
}
</script>

<style scoped></style>
