<template>
  <div class="payplugUIIOptions" :class="optionsClassName">
    <template v-for="item in optionsItems" :key="item">
      <label :class="item?.className + ' ' + (item?.disabled ? '-disabled' : '')" @click="changed(item?.value)"
      :data-e2e-name="optionsData + item?.label.replaceAll(/\s/g, '')">
        <input type="radio" :name="optionsName" :value="item?.value"
            :checked="((optionsSelected && item?.value && optionsSelected == item?.value) || (item?.checked)) ? 'checked' : null"
            :disabled="(item?.disabled) ? 'disabled' : null"/>
        <span> {{ item?.label }} <span v-if="item?.subText">{{ item?.subText }}</span></span>
      </label>
    </template>
  </div>
</template>

<script>
import { store } from "@/store/store";

export default {
  name: "AtomOptions",
  props: {
    optionsItems: {
      type: Object,
      required: true
    },
    optionsName: {
      type: String,
      required: false
    },
    optionsClassName: {
      type: String,
      required: false,
      default: ""
    },
    optionsSelected: {
      type: String,
      required: false
    },
    optionsData: {
      type: String,
      required: false
    },
  },
  data() {
    return {
      store: store
    }
  },
  created() {
    let selected = "";
    this.optionsItems.map(option => {
      if(option.checked)
        selected = option.value
    })
    this.store.storeValue(this.optionsName, selected+"")
  },
  methods: {
    changed(value) {
      store.storeValue(this.optionsName, value)
      if(this.optionsName == "payplug_sandbox") {
        store.storeValue("environment", (value == 0) ? "live" : "sandbox")
      }
    }
  }
}
</script>

