<template>
  <section class="payplugUiBlock generalBlock -subscribe -center" data-e2e-name="generalSubscribe">
    <BlockHeader :blockHeaderTitle="title" :blockHeaderDescription="description" />
    <div class="_content">
      <Paragraph :paragraphText="label" />
      <div class="_buttons">
        <ButtonLink :buttonLinkHref="link_create_account.url" :buttonLinkText="link_create_account.text" :buttonLinkTitle="link_create_account.text" :buttonLinkTarget="link_create_account.target"
                    data-e2e-name="payplugRegistration"/>
        <PayplugButton :buttonText="already_have_account" :class="'-tertiary'" @click="showLogin"
                       data-e2e-name="payplugCustomer"/>
      </div>
    </div>
  </section>
</template>

<script>
import PayplugButton from "../atoms/AtomButton";
import Paragraph from "../atoms/AtomParagraph";
import ButtonLink from "../atoms/AtomButtonLink";
import BlockHeader from "../molecules/MoleculeBlockHeader";


export default {
  name: "OrganismSectionSuscribe",
  components:{ ButtonLink, Paragraph, PayplugButton, BlockHeader },
  props:["environment"],
  data(){
    return{
      "title": this.jsonData.subscribe.title,
      "description": null,
      "already_have_account": null,
      "label": null,
      "link_create_account": null
    }
  },
  created() {
    this.init()
  },
  updated() {
    this.init()
  },
  methods: {
    init(){
      this.description = this.jsonData.subscribe.descriptions[this.environment].description;
      this.already_have_account = this.jsonData.subscribe.descriptions[this.environment].already_have_account;
      this.link_create_account = this.jsonData.subscribe.descriptions[this.environment].link_create_account;
      this.label = this.jsonData.subscribe.descriptions[this.environment].content_description;
    },
    showRegistration(){
      this.$emit('notRegisted', true)
    },
    showLoggedIn(){
      this.$emit('showLoggedIn', true)
    },
    showLogin(){
      this.$emit('notRegisted', false)
    }
  }
}
</script>

