<template>
  <section class="payplugUiBlock generalBlock -login" data-e2e-name="generalLogin">
    <BlockHeader :blockHeaderTitle="title" :blockHeaderDescription="description" />
    <div class="_content">
      <form class="_loginForm">
        <InputLabel :inputLabelClass="'_email'" :inputLabelType="'email'" :inputLabelName="'payplug_email'" :inputLabelPlaceholder="email_placeholder" :inputLabelLabel="email_label"
                    inputLabelData="payplugEmail"/>
        <InputLabel :inputLabelClass="'_password'" :inputLabelType="'password'" :inputLabelName="'payplug_password'" :inputLabelPlaceholder="password_placeholder" :inputLabelLabel="password_label"
                    inputLabelData="payplugPassword"/>
        <div>
          <PayplugButton :class="disabled_button ? '-disabled' : ''" class="payplugUIButton _connexion" :buttonText="connect" @click="doLogin"
                         data-e2e-name="payplugConnexion"/>
          <PayplugButton class="payplugUIButton _subscribe -tertiary" :buttonText="not_registered" @click="showRegistration"
                         data-e2e-name="payplugShowRegistration"/>
        </div>
        <div>
          <Anchor class="_forgotPassword" :anchorHref="forget_pasword.url" :anchorTarget="forget_pasword.target" :anchorTitle="forget_pasword.text" :anchorText="forget_pasword.text"
                  data-e2e-name="payplugForgotPassword"/>
        </div>
      </form>
    </div>
  </section>
</template>

<script>
import BlockHeader from "../molecules/MoleculeBlockHeader";
import InputLabel from "../molecules/MoleculeInputLabel";
import PayplugButton from "../atoms/AtomButton";
import Anchor from "../atoms/AtomAnchor";
import { login } from "@/store/ajax";

export default {
  name: "OrganismSectionLogin",
  components:{ BlockHeader, InputLabel, PayplugButton, Anchor },
  props:{
    logged:{
      type: Boolean,
      required: true,
      default: true
    },
    environment:{
      type: String,
      required: true,
    }
  },
  data(){
    return {
      "title":this.jsonData.login.title,
      "description": null,
      "form" : null,
      "not_registered": null,
      "connect": null,
      "forget_pasword": null,
      "disabled_button": false,
    }
  },
  created() {
    this.init()
  },
  updated() {
    this.init()
  },
  methods: {
    init(){
      this.description = this.jsonData.login.descriptions[this.environment].description;
      this.not_registered = this.jsonData.login.descriptions[this.environment].not_registered;
      this.connect = this.jsonData.login.descriptions[this.environment].connect;
      this.forget_pasword = this.jsonData.login.descriptions[this.environment].link_forgot_password
      this.form = this.jsonData.login.options;
      this.email_label = this.jsonData.login.descriptions[this.environment].email_label;
      this.email_placeholder = this.jsonData.login.descriptions[this.environment].email_placeholder;
      this.password_label = this.jsonData.login.descriptions[this.environment].password_label;
      this.password_placeholder = this.jsonData.login.descriptions[this.environment].password_placeholder;
    },
    showRegistration(){
      this.$emit('notRegisted', true)
    },
    async doLogin(){
      this.disabled_button = true;
      const response = await login();
      if(!response?.data?.settings?.logged) {
        if(response?.data?.settings?.login_error != "") {
          alert(response?.data?.settings?.login_error)
        }
      } else{
        this.$emit('showLoggedIn', true)
      }
      this.disabled_button = false;
    }
  }
}
</script>

