<template>
  <div class="payplugConfiguration">
    <div class="payplug">
      <br/><br/>
      <pre v-if="showStore" class="store">{{store}}</pre>
      <Header :environment="store.data.environment" />
      <Login :environment="store.data.environment" v-if="!logged && !register" v-on:notRegisted="updateparent" v-on:showLoggedIn="showLoggedIn" />
      <Subscribe :environment="store.data.environment" v-if="!logged && register" v-on:notRegisted="updateparent" />
      <LoggedIn :environment="store.data.environment" v-if="logged" v-on:showLogin="showLogin" />
      <PMethods :environment="store.data.environment" v-bind:class="{ '-disabled' : !logged}" />
      <Paylater :environment="store.data.environment" v-bind:class="{ '-disabled' : !logged}" />
      <Status :environment="store.data.environment" v-bind:class="{ '-disabled' : !logged}" />
      <payplugUIButton class="saveChangesButton" buttonText="Save changes" @click="saveData"  v-bind:class="{ '-disabled' : !logged}"
                       data-e2e-name="saveConfiguration"/>
    </div>
    <a href="/" @click.prevent="showStore = ! showStore"><b><i>Store</i></b></a>
  </div>
</template>

<script>
import Header from "@/components/organisms/OrganismSectionHeader";
import Login from "@/components/organisms/OrganismSectionLogin";
import Subscribe from "@/components/organisms/OrganismSectionSubscribe";
import LoggedIn from "@/components/organisms/OrganismSectionLoggedIn";
import Paylater from "@/components/organisms/OrganismSectionPaylater";
import Status from "@/components/organisms/OrganismSectionStatus";
import PMethods from "@/components/organisms/OrganismSectionPMethods";
import payplugUIButton from "@/components/atoms/AtomButton";
import { store } from "@/store/store";
import { save } from "@/store/ajax";

export default {
  name: "app",
  components:{ Header, Login, Subscribe, LoggedIn, Status, PMethods, Paylater, payplugUIButton },
  data(){
    return {
      store: store,
      logged: this.jsonData.settings.logged,
      register: false,
      showStore: false,
    }
  },
  created() {
    this.init();
  },
  updated() {
    this.init();
  },
  methods: {
    init(){
      for (let [key, value] of Object.entries(this.jsonData.settings.WP)) {
        this.store.storeValue(key, value, "settings");
      }
    },
    updateparent(status) {
      this.register = status
    },
    showLoggedIn() {
      this.logged = true
    },
    showLogin() {
      this.logged = false
    },
    saveData() {
      save();
    }
  }
}
</script>

