<template>
  <Switch :switchName="paylater.name" :switchChecked="paylater.checked ? true : null"
          switchData="paymentMethod_oney"/>
  <PaylaterHeader :description="description" :title="paylater.title" :links="links" />
  <PaylaterInformations v-if="store.data.oney" :environment="environment" :paylater="paylater" />
</template>

<script>
import Switch from "@/components/atoms/AtomSwitch";
import PaylaterHeader from "@/components/organisms/Paylater/OrganismPaylaterHeader";
import PaylaterInformations from "@/components/organisms/Paylater/OrganismPaylaterInformations";
import {store} from "@/store/store";

export default  {
  name: "OrganismPaylaterFull",
  components:{ Switch, PaylaterHeader, PaylaterInformations },
  props: ["environment", "paylater"],
  data(){
    return {
      store: store,
    }
  },
  created() {
    this.init()
  },
  updated() {
    this.init()
  },
  methods: {
    init(){
      this.description = this.paylater.descriptions[this.environment].description;
      this.links = this.paylater.descriptions[this.environment].link_know_more ?? null;
    },
    showRegistration(){
      this.$emit('notRegisted', true)
    },
    showLoggedIn(){
      this.$emit('showLoggedIn', true)
    }
  }
}
</script>

