<template>
  <div class="_paylaterAdvancedOption"
       :class="'-' + advanced_option.name">
    <PaylaterImage :imageSrc="advanced_option.image_url" imgParentClass="_img"/>
    <div class="_wrapper">
      <Title :titleText="advanced_option.title" />
      <div v-if="advanced_option.descriptions" class="_text">
        <template v-if="'thresholds' == advanced_option.name">
          <Paragraph :paragraphText="advanced_option.descriptions.description" />
          <div class="_inputs">
            <div class="payplugUIInput -icon -iconEuro">
              <Input type="number"
                     step="1"
                     :name="advanced_option.descriptions.min_amount.name"
                     :inputName="advanced_option.descriptions.min_amount.name"
                     :inputValue="advanced_option.descriptions.min_amount.value"
                     :inputMin="advanced_option.descriptions.min_amount.min"
                     :inputMax="advanced_option.descriptions.min_amount.max"
                     :inputPlaceholder="advanced_option.descriptions.min_amount.placeholder"
                     @change="emitChange"
                     data-e2e-name="oneyThresholdMin" />
            </div>
            <Paragraph class="-inter" :paragraphText="advanced_option.descriptions.inter" />
            <div class="payplugUIInput -icon -iconEuro">
              <Input type="number"
                     step="1"
                     :name="advanced_option.descriptions.max_amount.name"
                     :inputName="advanced_option.descriptions.max_amount.name"
                     :inputValue="advanced_option.descriptions.max_amount.value"
                     :inputMin="advanced_option.descriptions.max_amount.min"
                     :inputMax="advanced_option.descriptions.max_amount.max"
                     :inputPlaceholder="advanced_option.descriptions.max_amount.placeholder"
                     @change="emitChange"
                     data-e2e-name="oneyThresholdMax" />
            </div>
          </div>
          <Paragraph v-if="thresholdsError" class="_error" :paragraphText="advanced_option.descriptions.error.text" />
        </template>
        <template v-if="'thresholds' != advanced_option.name">
          <PaylaterOptionDescription
              v-for="description in advanced_option.descriptions"
              :key="description"
              :description="description" />
        </template>
      </div>
    </div>
    <div class="_switch">
      <Switch v-if="advanced_option.switch" :switchName="advanced_option.name" :switchChecked="advanced_option.checked ? true : null"
              :switchData="switchData"/>
    </div>
  </div>
</template>

<script>
import PaylaterImage from "@/components/atoms/AtomImage";
import Title from "@/components/atoms/AtomTitle";
import Switch from "@/components/atoms/AtomSwitch";
import Paragraph from "@/components/atoms/AtomParagraph";
import Input from "@/components/atoms/AtomInput";
import Anchor from "@/components/atoms/AtomAnchor";
import PaylaterHeader from "@/components/organisms/Paylater/OrganismPaylaterHeader";
import PaylaterOptionDescription from "@/components/organisms/Paylater/OrganismPaylaterOptionDescription";
import {store} from "@/store/store";

export default {
  name: "OrganismPaylaterOption",
  components:{ Switch, PaylaterHeader, PaylaterImage, Title, PaylaterOptionDescription, Paragraph, Input, Anchor },
  props: ["environment", "advanced_option"],
  data(){
    return {
      store: store,
      thresholdsError: this.thresholdsError,
      switchData: ""
    }
  },
  created() {
    this.init()
  },
  updated() {
    this.init()
  },
  methods: {
    init(){
      this.thresholdsError = 'thresholds' == this.advanced_option.name
          && (
              parseInt(this.store.data?.oney_min_amounts) < parseInt(this.advanced_option.descriptions?.min_amount.min)
              || parseInt(this.store.data?.oney_min_amounts) > parseInt(this.advanced_option.descriptions?.min_amount.max)
              || parseInt(this.store.data?.oney_max_amounts) < parseInt(this.advanced_option.descriptions?.max_amount.min)
              || parseInt(this.store.data?.oney_max_amounts) > parseInt(this.advanced_option.descriptions?.max_amount.max)
              || parseInt(this.store.data?.oney_max_amounts) < parseInt(this.store.data?.oney_min_amounts)
          )
          ? true : false;

      if(this.advanced_option.name == "optimized")
        this.switchData = "optimizedSwitch";
      else if(this.advanced_option.name == "product")
        this.switchData = "productOneyCtaTitleSwitch";
      else if(this.advanced_option.name == "cart")
        this.switchData = "cartOneyCtaTitleSwitch";
    },
    emitChange() {
      this.thresholdsError = 'thresholds' == this.advanced_option.name
      && (
          parseInt(this.store.data?.oney_min_amounts) < parseInt(this.advanced_option.descriptions?.min_amount.min)
          || parseInt(this.store.data?.oney_min_amounts) > parseInt(this.advanced_option.descriptions?.min_amount.max)
          || parseInt(this.store.data?.oney_max_amounts) < parseInt(this.advanced_option.descriptions?.max_amount.min)
          || parseInt(this.store.data?.oney_max_amounts) > parseInt(this.advanced_option.descriptions?.max_amount.max)
          || parseInt(this.store.data?.oney_max_amounts) < parseInt(this.store.data?.oney_min_amounts)
      )
          ? true : false;
    }
  }
}
</script>

