<template>
  <div class="payplugUISwitch" :class="(switchDisabled) ? '-disabled': ''">
    <label class="_switch" :data-e2e-name="switchData">
      <input type="checkbox" @change="emitChange" :disabled="(switchDisabled) ? 'disabled' : null" :checked="(switchChecked) ? 'checked' : null" :name="switchName">
      <span class="_slider"></span>
      <span v-if="switchEnabledLabel" class="_label"
            :data-enable-text="(switchEnabledLabel) ? switchEnabledLabel : null"
            :data-disable-text="(switchDisabledLabel) ? switchDisabledLabel : null"></span>
    </label>
  </div>
</template>

<script>
import { store } from "@/store/store";

export default {
  name: "AtomSwitch",
  props: {
    switchName: {
      type: String,
      required: false
    },
    switchDisabled: {
      type: String,
      required: false
    },
    switchChecked: {
      type: String,
      required: false
    },
    switchEnabledLabel: {
      type: String,
      required: false
    },
    switchDisabledLabel: {
      type: String,
      required: false
    },
    switchData: {
      type: String,
      required: false
    },
  },
  data() {
    return {
      store: store
    }
  },
  created() {
    this.store.storeValue(this.switchName, this.switchChecked || false)
  },
  methods: {
    emitChange(e) {
      this.store.storeValue(this.switchName, e.target.checked)
    }
  }
}
</script>

