<template>
  <section class="payplugUiBlock paymentMethodBlock " data-e2e-name="paymentMethodsBlock">
    <BlockHeader :blockHeaderTitle="title" :blockHeaderDescription="description" />
    <div class="_content">
      <div class="paymentOption" :class="'-' + payment_method.name" v-for="payment_method in payment_methods" :key="payment_method">
        <PMethodFull :environment="environment" :payment-method="payment_method" />
      </div>
    </div>
  </section>
</template>

<script>
import BlockHeader from "../molecules/MoleculeBlockHeader";
import PMethodFull from "./PMethod/OrganismPMethodFull";

export default {
  name: "OrganismSectionPMethods",
  components:{ BlockHeader, PMethodFull },
  props: ["environment"],
  data(){
    return {
      "title": this.jsonData.payment_methods.title,
      "description": null,
      "payment_methods": []
    }
  },
  created() {
    this.init()
  },
  updated() {
    this.init()
  },
  methods: {
    init(){
      this.description = this.jsonData.payment_methods.descriptions[this.environment].description;
      this.payment_methods = this.jsonData.payment_methods.options;
    },
    showRegistration(){
      this.$emit('notRegisted', true)
    },
    showLoggedIn(){
      this.$emit('showLoggedIn', true)
    }
  }
}
</script>

